.footer-wrapper {
	width: 100%;
	margin-left: auto;
	margin-right: auto;

	display: flex;
	justify-content: center;
	z-index: 10;
	margin-top: 6em;
}

.footer {
	width: 100%;
	background-color: #ffffff;
}

.footer-links a {
	font-weight: normal;
	color: #fff;
	font-family: 'Noto Sans';
	font-size: 12pt;
}

.footer p {
	font-family: "Lato";
	margin: 0;
	position: relative;
	text-transform: capitalize;
	font-size: 16px;
	font-weight: bold;
	bottom: 1px;
}

.footer-button-wrapper {
	background-color: #63808c;
	display: flex;
	justify-content: space-evenly;
	padding: 12px 0;
	position: fixed;
	bottom: 0;
	left: 0;
	width: 100%;
}

.footer-wrapper .footer button {
	color: #ffffff;
	z-index: 10;
	width: 200px;
	display: flex;
	justify-content: space-evenly;
}

.footer-social-wrapper {
	display: none;
}

.footer-wrapper .start-order {
	display: none;
}

.footer-wrapper .start-order.show-start-order {
	display: inline-block;
}

.footer-wrapper .cart {
	display: none;
}

.footer-wrapper .cart.show-cart {
	display: inline-block;
}

.footer-wrapper .checkout {
	display: none;
}

.footer-wrapper .checkout.show-checkout {
	display: inline-block;
}

.footer h4 {
	display: none;
}

.footer-links-wrapper {
	background-color: #63808c;
}

.footer-links h2 {
	margin-bottom: 0;
}

.footer-links {
	margin-left: auto;
	margin-right: auto;
	padding: 0.83em 0;
	display: flex;
	flex-wrap: wrap;
	width: 100%;
}

.footer-links ul {
	width: 50%;
	padding-left: 1em;
}

.footer-links li {
	margin: 10px 0;
	margin-left: .5em;
	width: auto;
}

.footer-links li span {
	cursor: pointer;
}

@media screen and (min-width: 766px) {

	.footer-links {
		display: flex;
		justify-content: unset;
		flex-wrap: nowrap;
		padding-top: 0;
		text-align: left;
		max-width: 1138px;
		padding: 0 6em;
		margin-bottom: 30px;
	}

	.footer-links li {
		margin: 5px 0;
		margin-left: .5em;
	}

	.footer-links ul {
		min-width: 0;
	}

	

	.footer-social-wrapper {
		grid-template: 1fr / 1fr 1fr 1fr;
		padding: 0;
	}

	.footer-social-icons {
		grid-column: 2;
		margin-top: auto;
		margin-bottom: auto;
	}

	.copyright-text {
		grid-column: 3;
	}

	.footer-wrapper {
		width: 100%;
		position: relative;
	}

	.footer {
		background-color: #63808c;
	}

	.footer-button-wrapper {
		max-width: 1366px;
		justify-content: space-evenly;
		display: none;
	}

	.footer-social-wrapper {
		display: grid;
		max-width: 1138px;
		text-align: center;
		margin-left: auto;
		margin-right: auto;
		color: #ffffff;
		font-weight: bold;
		padding: 12px;
	}

	.footer-social-icons ul {
		margin: auto;
	}

	.footer-wrapper .open-locker {
		display: none;
	}

	.footer-wrapper .copyright-text {
		font-size: .7em;
		margin: auto;
		font-weight: normal;
	}

}

@media screen and (min-width: 1336px) {
	.footer-links {
		padding: 0 12em;
	}
}
