#map div button {
    display: none;
}

a, u {
    text-decoration: none;
}

a:visited {
    color: #ECEFF1
}

#map div:nth-child(1) {
    /* border-radius: 24px; */
}

body #content #map div:nth-child(1) {
    border-radius: 24px;
    height: inherit;
    width: inherit;
}

/*.header-on-map-wrapper {*/
    /*display: none;*/
/*}*/

.header-on-map-wrapper {
    display: block;
    margin: 0 auto;
    top: 120px;
    left: 50%;
    transform: translate(-50%);
    position: absolute;
    color: #0194f6;
    text-align: center;
}

#map .header-on-map-wrapper .header-on-map {
    display: inline-block;
    width: auto;
    padding: 1vh;
    background-color: #0194f6;
    /* border-radius: 24px */
}

.header-on-map h1 {
    display: flex;
    text-align: center;
}

@media screen and (max-width: 766px) {
    .header-on-map h1 {
        font-size: 14pt;
    }

    .header-on-map-wrapper {
        bottom: 50px;
        top: auto;
    }
}

@media screen and (min-width: 766px) {

    #map div:nth-child(1) {
        border-radius: 0px;
    }

    body #content #map div:nth-child(1){
        border-radius: 0;
        width: 100%;
    }

    .map {
        width: 100%;
    }


}