ul, ol {
    padding: 0;
    margin:0;
    list-style: none;
}

.styled-list {
    margin-left: 15px;
    list-style: initial;
    padding-left: 20px;
}

h1 {
    margin: 0;
}
.start-your-order-fab button {
    margin-top: 1.5em;
}

.start-your-order p {
    margin: 0;
}

.product-header {
    display: flex;
}

.product-header p {
    margin: .5em 0;
}

.blue-text {
    color: #3993F3;
}

.product-header .blue-text {
    margin-left: 1ex;
}

main {
    max-width: 1138px;
    margin: 0 auto;
}

.styled-list li {
    margin: 1em 0;
}

.action-shot-wrapper, .react-parallax-content {
    padding-top: 87px;
    height: 600px;
    background: linear-gradient(rgba(105, 105, 105, 0.3), rgba(0, 0, 0, .3));
    display: flex;
    align-items: center;
    justify-content: center;
    background-position: right;
    background-size: cover;
}

.span-left {
    margin-left: 0;
    color: #fff;
    text-align: center;
}

.snake-wrapper img {
    max-width: 50%;
}

.snake-wrapper li {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.home-product-wrapper {
    display: flex;
    justify-content: space-between;
    padding-top: 5em;
    flex-direction: column;
}

.home-product-wrapper li {
    flex: 1;
}

.home-product-wrapper li:not(:last-child) {
    margin-right: 25px;
}

.content main{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 1em;
}

.MuiFab-extended-5.MuiFab-primary-3 {
    box-shadow: none;
}

.overlap-wrapper .large-card {
    padding: 1.5em;
    align-self: stretch;
    margin: 0 2em -2em 0;
}

.overlap-wrapper {
    display: flex;
    align-items: center;
    margin-bottom: 10em;
    flex-direction: column;
}

.overlap-wrapper:last-child {
    margin-bottom: 0;
}

.overlap-wrapper > * {
    flex: 1;
}

.overlap-wrapper > div {
    background: #63808c;
}

.small-card {
    padding: 1em;
    color: #fff;
    margin-left: 2em;
    position: relative;
    z-index: 1;
}
/*.overlap-wrapper .large-card {*/
    /*margin: -2em 2em 0 0*/
/*}*/

.overlap-wrapper-reverse {
    flex-direction: column-reverse;
}

.home-snake-assets,
.technology-snake-assets {
    padding: 5em 1em 3em;
}

.content .platform-wrapper {
    padding: 2em 1em 0;
}

.small-header .react-parallax-content {
    height: 300px;
}

.overlap-wrapper img {
    background-color: #f0f0f0;
}

.how-it-works {
    padding-bottom: 6em;
}

.platform-wrapper .how-it-works {
    padding-bottom: 0;
}

.overlap-wrapper:nth-child(2) {
    margin-bottom: 0;
}

.content .how-it-works-title {
    align-self: flex-start;
    margin-top: 1em;
    color: #EF7A6B;
}

.left-space {
    margin-left: 20px;
}

.content .how-it-works-title {
    padding-top: 90px;
    margin-top: calc(-90px + 1em);
}

.overlap-wrapper {
    padding-top: 90px;
    margin-top: calc(-90px);
}

#platform-design,
#platform-what-we-do {
    padding-top: calc(90px + 1em);
    margin-top: -90px;
}

.platform-button {
    width: 100%;
    display: flex;
    justify-content: center;
}

#root .content .home-wrapper .react-parallax-bgimage {
    left: 150% !important;
}

@media screen and (min-width: 766px) {

    #root .content .home-wrapper .react-parallax-bgimage {
        left: 50% !important;
    }

    .action-shot-wrapper, .react-parallax-content {
        padding-top: 90px;
    }

    .technology-snake-assets {
        padding: 3em 7.33em;
    }
    
    .home-snake-assets {
        padding: 3em 5em;
    }
    
    .trimet {
        padding: 1em;
    }

    .small-card {
        margin-left: 0;
    }

    .overlap-wrapper:nth-child(2) {
        margin-bottom: 10em;
    }

    .platform-hr {
        display: none;
    }

    .how-it-works {
        padding: 0 0 3em;
    }

    .overlap-wrapper .large-card {
        margin: -3em;
        margin-left: 0.4em;
    }

    .overlap-wrapper .large-card:last-child {
        margin: -3em;
        margin-right: 0.4em;
    }

    .overlap-wrapper {
        flex-direction: row;
        width: 110%;
        padding-top: 90px;
        margin-top: calc(-90px + 5em);
    }

    .snake-wrapper li {
        flex-direction: row;
    }

    .home-product-wrapper {
        flex-direction: row;
    }

    .action-shot-wrapper, .react-parallax-content {
        justify-content: normal;
    }

    .content {
        margin-top: 0;
        padding-top: 0;
        margin-left: 0;
        margin-right: 0;
    }

    .span-left {
        margin-left: 15%;
        text-align: left;
    }

    .action-shot-wrapper .span-left h1 {
        margin-bottom: 12px;
    }

    .action-shot-wrapper .span-left h2 {
        margin-top: 0;
    }

    .snake-wrapper li {
        position: relative;
    }

    .snake-wrapper li:not(:first-child) {
        padding-top: 10em;
    }

    .snake-wrapper li:not(:first-child)::after {
        content: '';
        width: 62%;
        height: 200px;
        left: 50%;
        transform: translate(-50%);
        background: url(../assets/bottomLine.svg);
        background-repeat: no-repeat;
        position: absolute;
        top: 0;
    }

    .snake-wrapper li:not(:first-child):nth-child(even)::after {
        background: url('../assets/topLine.svg');
        background-size: 100%;
        background-repeat: no-repeat;
        position: absolute;
        top: 0;
    }

    .snake-wrapper li:nth-child(even) {
        flex-direction: row-reverse;
        text-align: right;
    }

    .header-text {
		width: 1138px;
		margin: auto;
		padding: 0 1em;
    }
    
    .content .platform-wrapper {
        padding: 5em 1em;
    }

}

@media screen and (max-width: 1366px) {

    .overlap-wrapper img {
        background-color: #e8e8e8;
    }

    .overlap-wrapper {
        width: 100%;
    }
}