.team-wrapper ul {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-evenly;
}

.team-wrapper li {
	width: 14em;
	height: 14em;
	position: relative;
	margin: 1em 3px;
	overflow: hidden;
	max-width: 33%;
}

.team-wrapper li div {
	opacity: 0;
	transition: 0.1s;

}

.team-wrapper li:hover div {
	opacity: 1;
}

.team-wrapper div {
	background-color: #0194f6d9;
	position: absolute;
	top:0;
	width: 100%;
	padding: 1em;
	text-align: left;
	font-size: 1em;
	height: 80%;
}

.team-wrapper p {
	word-wrap: break-word;
	margin: 0.5em 0;
}

.about-wrapper {
	margin: 5em auto 0;
}

#about-chupt,
#meet-the-team {
	padding-top: 90px;
	margin-top: -90px;
}

@media screen and (max-width: 766px) {
	.team-wrapper li {
		min-width: 14em;
		/*height: 141.11px;*/
		overflow: hidden;
	}

	.team-wrapper {
		justify-content: center;
	}

	.who-we-are-text {
		text-align: left;
	}

	.team-wrapper div {
		background-color: #0194f6d9;
		position: absolute;
		top:0;
		width: 100%;
		padding: 1em;
		text-align: left;
		font-size: 1em;
		height: 80%;
		
	}

	#meet-the-team img {
		/* max-height: 141.11px;
		min-width: 141.11px; */
	}


}
