@import url('https://fonts.googleapis.com/css?family=Noto+Sans:400,400i,700,700i|Roboto:500,700,900');

.title-lg {
    font-family: 'Roboto';
    font-size: 42pt;
    font-weight: 900;
    color: #333333;
}

.title-md {
    font-family: 'Roboto';
    font-size: 30pt;
    font-weight: 700;
    color: #616161;
}

.title-sm, .footer-links h2 a {
    font-family: 'Roboto';
    font-size: 20pt;
    font-weight: 500;
    color: #616161;
}


.text {
    font-family: 'Noto Sans';
    font-size: 14pt;
    font-weight: 400;
    color: #69808B;
}

.salmon {
    color: #EF7A6B;
    font-weight: 700;
}

.blue {
    color: #3993F3;
}

.white, .footer-links h2 a {
    color: #ECEFF1;
}

.salmon-bg {
    background-color: #EF7A6B;
}

.blue-bg {
    background-color: #3993F3;
}

.bold {
    font-weight: 700;
    font-size: 1.1em;
}

.bold-white {
    font-weight: 700;
    font-size: 1.1em;
    color: #fafafa;
}


.bold-on-salmon {
    font-weight: 700;
    font-size: 1.1em;
    color: #7e57c2;
}

.bold-on-blue {
    font-weight: 700;
    font-size: 1.1em;
    color: #FFF176;
}

a, a:visited{
    color: #3993F3;
    font-weight: 700;
    transition-duration: 250ms;
    transition-timing-function: ease-in-out;
}

.header-wrapper a:hover,
.footer-links a:hover {
    opacity: .5;
}

sup {
    font-size: 0.55em;
}

.spacer-lg {
    position: relative;
    height: 12vh;
}

.spacer-md_lg {
    position: relative;
    height: 3vh;
}

.spacer-md {
    position: relative;
    height: 15px;
}

.background {
    padding-top: 5vh;
    padding-bottom: 5vh;
}

.shift {
    padding-left: 10vw;
}

.center-text {
    text-align: center;
}

.phantomDiv {
    height: 87px;
}

@media only screen and (min-width: 766px) {
  .title-lg {
    font-size: 36pt;
  }
  
}

@media only screen and (min-width: 1366px) {
    .phantomDiv {
        height: 90px;
        }
}
