.mod-card-wrapper {
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;
}

.mod-card-wrapper img {
    max-width: inherit;
    max-height: inherit;
    margin-left: auto;
    margin-right: auto;
}

.mod-item-button button {
    background-color: #00D100;
    color: #ffffff;
    font-size: 16px;
    margin: 12px 24px 24px;
    border-radius: 24px;
    font-weight: bold;
    padding: 6px 12px;
}

.mod-item-button button:hover {
    background-color: #00D100;
}

.mod-item-button button:active {
    background-color: #0194F6;   
}

.mod-item-description {
    margin-top: 0;
    color: #7F7F7F;
}

.mod-card-images-wrapper {
    max-height: 250px;
    max-width: 300px;
    margin-left: auto;
    margin-right: auto;
}

.mod-card-images-wrapper img:nth-child(2) {
    position: relative;
    bottom: 112.5px;
    right: 75px;
    width: 50%;
}

.order-content {
    margin-top: 24px;
}

.add-to-cart-button button {
    width: 155px;
}

.add-to-cart-button {
    display: flex;
    justify-content: center;
}

@media screen and (min-width: 1366px) {

    .mod-item-card {
        display: grid;
        grid-template: 1fr / 1fr 1fr;
    }

    .mod-item-text-wrapper h3 {
        font-size: 36px;
    }

    .mod-item-text-wrapper p {
        font-size: 18px;
    }

    .mod-card-images-wrapper {
        max-height: 300px;
        grid-row: 1;
        grid-column: 1;
    }

    .mod-card-images-wrapper img:nth-child(2) {
        bottom: 140px;
    }

    .mod-item-button {
        grid-row: 1;
        grid-column: 2;
        margin-top: auto;
    }

    .mod-item-button button {
        width: 210px;
    }

    .add-to-cart-button {
        grid-row: 2;
        grid-column: 2;
        margin-bottom: 0;
        height: 88px;
    }

    .add-to-cart-button button {
        margin: 24px 0;
    }

    .food-mod-wrapper {
        grid-row: 1;
        grid-column: 2;
    }

    .side-swapper {
        grid-row: 1;
        grid-column: 1;
        margin: 0;
        margin-top: auto;
        margin-bottom: 0;
    }

}