.menu-card-wrapper {
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 3em;
}

.menu-card-wrapper img {
    max-width: inherit;
    max-height: inherit;
    margin-left: auto;
    margin-right: auto;
}

.menu-item-button button {
    background-color: #00D100;
    color: #ffffff;
    font-size: 16px;
    /* margin: 12px 24px 24px; */
    border-radius: 24px;
    font-weight: bold;
    padding: 6px 12px;
}

.menu-item-button button:hover {
    background-color: #00D100;
}

.menu-item-button button:active {
    background-color: #0194F6;   
}

.menu-item-description {
    margin-top: 0;
    /* color: #7F7F7F; */
}

.menu-card-images-wrapper {
    max-height: 250px;
    max-width: 300px;
    margin-left: auto;
    margin-right: auto;
}

.menu-card-images-wrapper img:nth-child(2) {
    position: relative;
    bottom: 112.5px;
    right: 75px;
    width: 50%;
}

.order-content {
    margin-top: 24px;
}

.menu-item-button {
    display: flex;
    justify-content: center;
    margin-top: auto;
    padding-top: 1em;
    /* width: 100%; */
}

.menu-item-button button {
    min-width: 300px;
}

@media screen and (min-width: 1366px) {

    /* .menu-card-wrapper img {
        max-width: 400px;
    } */

    .menu-card-wrapper {
        max-width: 1138px;
        margin-bottom: 0;
    }

    .menu-item-text-wrapper {
        display: inline-block;
        margin-top: 12px;
        margin-right: 0;
        grid-row: 1;
        grid-column: 2;
    }

    .menu-item-text-wrapper p {
        font-size: 18px;
    }

    .menu-card-images-wrapper {
        max-height: 300px;
        /* margin-left: 24px; */
    }

    .menu-card-images-wrapper img:nth-child(2) {
        bottom: 140px;
    }

    .menu-item-card {
        display: grid;
        grid-template: 1fr / 1fr 1fr;
    }

    .menu-item-button button {
        width: 210px;
        margin: 12px 24px 24px;
    }

}