.test-wrapper {
    display: flex;
    justify-content: center;
    width: 100%;
}

.orderNavWrapper {
    position: fixed;
    display: flex;
    justify-content: space-evenly;
    width: 100%;
    padding-top: 1em;
    padding-bottom: 12px;
    max-width: 400px;
    background-color: #ffffff;
    z-index: 1;
}

.orderNavWrapper p {
    display: block;
    margin: 0;
}

.orderNavWrapper img {
    display: block;
}

.orderNavWrapper button {
    max-width: 75px;
    max-height: 75px;
    box-shadow: none;
    border: 2px solid #ffffff;
    border-radius: 12px;
}

.orderNavWrapper button:hover {
    background-color: #A1A1A1;
}

.orderNavWrapper button span:first-child {
    display: flex;
    flex-wrap: wrap;
}

.orderNavWrapper .active button {
    background-color: #A1A1A1;
}

.mealsButtonContainer button {
    background-color: #00D100;
}

.entreesButtonContainer button {
    background-color: #0093FA;
}

.sidesButtonContainer button {
    background-color: #FF7065;
}

.drinksButtonContainer button {
    background-color: #96CDFD;
}

.order-content {
    margin-top: 24px;
    padding: 0 3px;
}

@media screen and (min-width: 1366px) { 

    .test-wrapper {
        display: none;
    }

    /* 1366 - (1366/4) = 1024.5 */
    .orderNavWrapper {
        max-width: 1024.5px;
        /* justify-content: space-between; */
        margin-left: auto;
        margin-right: auto;
    }

    .orderNavWrapper button {
        font-size: 2em;
        font-weight: bold;
        max-width: 100%;
        max-height: 100%;
        border-radius: 24px;
        width: 284.5px;
        /* height: 164px; */
    }

}