img:not(.react-parallax-bgimage) {
	max-width: 100%;
	display: block;
}

#watermark a,
#watermark a:visited {
	color: #ECEFF1;
}

#watermark a:hover {
	cursor: pointer;
}



#watermark,
#timer {
	color: rgb(0, 243, 0);
	font-size: 2em;
	position: fixed;
	width: 100%;
	margin: 0!important;
	z-index: 100;
	top: 200px;
	display: flex;
	justify-content: center;
	background-color: rgba(0, 0, 0, 0.445);
}

#timer {
	background-color: transparent;
}

#watermark p,
#timer p {
	margin: 0;
	display: inline-block;
}

* {
	box-sizing: border-box;
}

.content-wrapper {
	display: flex;
	justify-content: center;
	background-color: transparent;
	margin-left: auto;
	margin-right: auto;
	position: static;
}

.content {
	min-width: 320px;
	min-height: calc(100vh - 64px);
	width: 100%;
	background: transparent;
}

hr {
	margin: 5em;
	width: 80%;
}

@media screen and (min-width: 766px) {
	#watermark,
	#timer {
		font-size: 3em;
	}

	#watermark p {
		/* cursor: pointer; */
	}
}

@media screen and (min-width: 1366px) {
	#watermark,
	#timer {
		font-size: 5em;
	}

	
}