@import url(https://fonts.googleapis.com/css?family=Lato);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.6.3/css/font-awesome.css);

.footer-social-icons a {
    text-decoration: none;
    color: #fff;
    width: 100%;
    height: 100%;
}
.footer-social-icons i {
    border-radius: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}
.footer-social-icons ul {
    margin-bottom: 24px;
}

p > a:hover{
    color: #d9d9d9;
    text-decoration:  underline;
}

.footer-social-icons h1,
.footer-social-icons h2,
.footer-social-icons h3,
.footer-social-icons h4,
.footer-social-icons h5,
.footer-social-icons h6 {
    margin: 24px 0 12px;
}
._12 {
    font-size: 1.2em;
}
._14 {
    font-size: 1.4em;
}
.footer-social-icons ul {
    padding:0;
    list-style: none;
}
.footer-social-icons {
    width: auto;
    display:block;
    margin: 0 auto;
}
.social-icon {
    color: #fff;
}
ul.social-icons {
    margin-top: 10px;
    display: flex;
    justify-content: space-evenly;
}
.social-icons li {
    vertical-align: top;
    display: flex;
    justify-content: center;
    justify-items: center;
    width: 40px;
    height: 40px;
    margin: 0 6px;
}
.social-icons a {
    color: #0194F6;
    text-decoration: none;
}
.fa-facebook, .fa-instagram {
    -o-transition:.5s;
    -ms-transition:.5s;
    -moz-transition:.5s;
    -webkit-transition:.5s;
    transition: .5s;
    background-color: #0194F6;
    color: #fff;
}
.fa-facebook:hover {
    color: #3d5b99;
}
.fa-twitter {
    -o-transition:.5s;
    -ms-transition:.5s;
    -moz-transition:.5s;
    -webkit-transition:.5s;
    transition: .5s;
    background-color: #0194F6;
    color: #fff;
}
.fa-twitter:hover {
    color: #00aced;
}
.fa-rss {
    padding:10px 14px;
    -o-transition:.5s;
    -ms-transition:.5s;
    -moz-transition:.5s;
    -webkit-transition:.5s;
    transition: .5s;
    background-color: #322f30;
}
.fa-rss:hover {
    background-color: #eb8231;
}
.fa-youtube {
    padding:10px 14px;
    -o-transition:.5s;
    -ms-transition:.5s;
    -moz-transition:.5s;
    -webkit-transition:.5s;
    transition: .5s;
    background-color: #322f30;
}
.fa-youtube:hover {
    background-color: #e64a41;
}
.fa-linkedin {
    padding:10px 14px;
    -o-transition:.5s;
    -ms-transition:.5s;
    -moz-transition:.5s;
    -webkit-transition:.5s;
    transition: .5s;
    background-color: #322f30;
}
.fa-linkedin:hover {
    background-color: #0073a4;
}
.fa-google-plus {
    padding:10px 9px;
    -o-transition:.5s;
    -ms-transition:.5s;
    -moz-transition:.5s;
    -webkit-transition:.5s;
    transition: .5s;
    background-color: #322f30;
}
.fa-google-plus:hover {
    background-color: #e25714;
}

/* CUSTOM */

i.fa.fa-instagram::before {
    /* position: relative;
    right: 1.5px; */
}

.fa-instagram {
    -o-transition:.5s;
    -ms-transition:.5s;
    -moz-transition:.5s;
    -webkit-transition:.5s;
    transition: .5s;
}

.fa-instagram:hover {
    color: #bc1888; 
}

@media screen and (min-width: 766px) {
    
    .fa-facebook, .fa-instagram, .fa-twitter {
        background-color: #fff;
        color: #63808c;
    }

}