.header-wrapper {
	width: 100%;
	position: fixed;
	top: 0;
	left: 0;
	/* height: 97px; */
	z-index: 10;
	background: transparent;
}

.header-wrapper * {
	box-sizing: unset;
}

.top-header {
	/* margin-top: 0;
	margin-bottom: 0;
	width: 100%; */
}

.banner {
	background-color: #0194f6;
	width: 100%;
	display: grid;
	grid-template: 1fr / 1fr 1fr 1fr;
}

.header-wrapper p {
	font-family: "Lato";
	margin: 0;
	position: relative;
	text-transform: capitalize;
	font-size: 16px;
	font-weight: bold;
}

#logo {
	height: 75px;
	position: relative;
	display: inline-block;
	top: 6px;
	right: 9px;
	z-index: 10;
	grid-row: 1;
	grid-column: 2;
	max-width: 500px;
}

.header-logo-link {
	grid-row: 1;
	grid-column: 2;
	margin-left: auto;
	margin-right: auto;
}

#menuButton {
	width: 25px;
	height: 25px;
	margin: 17px 0;
	margin-left: auto;
	margin-right: auto;
	border-radius: 50px;
	padding: 6px;
	border: 2px solid black;
	display: inline-block;
	margin-left: auto;
}

.header-menu-wrapper {
	grid-row: 1;
	grid-column: 3;
	margin-left: auto;
}

.header-wrapper [role="tooltip"] {
	z-index: 1;
}

a {
	color: inherit;
	text-decoration: inherit;
}

.MuiButton-root-3 {
	padding: 0;
}

.desktop-links {
	display: none;
}

@media screen and (min-width: 1366px) {

	.header-menu-wrapper {
		display: none;
	}

	.header-wrapper {
		max-width: 100%;
		height: 90px;
		background-color: #0194f6;
	}

	.banner {
		max-width: 1138px;
		margin: auto;
		background: transparent;
		height: 90px;
	}
	/*    box-shadow: 0px -15px 0px -5px inset #ffffff6b;*/
	.header-logo-link {
		grid-column: 1;
		margin-left: 24px;
	}

	.desktop-links a, .desktop-links a:visited{
		color: #ffffff;
	}
	.desktop-links {
		margin: auto;
		margin-right: 0;
		width: auto;
		display: -webkit-flex;
		display: flex;
		justify-items: baseline;
		grid-column: 2 / span 3;
		z-index: 10;
	}

	.desktop-links p {
		padding: 0 12px;
		display: inline-block;
	}

	.desktop-links .secondary-links button {
		width: 180px;
	}

	.header-wrapper .primary-links {
		/* font-weight: normal; */
		margin-right: 12px;
		margin-top: auto;
		margin-bottom: auto;
	}

	.header-wrapper .primary-links p {
		font-size: 18px;
		font-weight: normal;
	}

	.start-order {
		display: none;
	}

	.start-order.show-start-order {
		display: inline-block;
	}

	.cart {
		display: none;
	}

	.cart.show-cart {
		display: inline-block;
	}

	.checkout {
		display: none;
	}

	.checkout.show-checkout {
		display: inline-block;
	}
}

