.orderWrapper {
    margin-top: 87px;
    /* margin-bottom: 76px; */
}

.menu-card-wrapper img {
    max-width: inherit;
    max-height: inherit;
    margin-left: auto;
    margin-right: auto;
}

.add-to-cart-button button {
    background-color: #00D100;
    color: #ffffff;
    font-size: 16px;
    margin: 48px 24px 24px;
    border-radius: 24px;
    font-weight: bold;
    padding: 6px 12px;
}

.add-to-cart-button button:hover {
    background-color: #00D100;
}

.add-to-cart-button button:active {
    background-color: #0194F6;   
}

.menu-wrapper {
    display: flex;
    /* flex-wrap: wrap; */
}

/* .menu-card-wrapper {
    min-width: 200px;
} */

.menu-card-wrapper {
    display: flex;
    flex-direction: column;
}

.menu-card-wrapper > img {
    max-width: inherit!important;
}

.category-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.coming-soon {
    padding: 1em;
}

.real-white {
    color: #fff;
}

.food-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.food-header p {
    margin-right: 1em; 
}

.menu-item-text-wrapper {
    margin-top: 1em;
}

.footer-header {
    padding-top: 90px;
    margin-top: -90px;
}


@media screen and (max-width: 1366px) {

    .footer-header {
        padding-top: calc(1em + 97px);
        margin-top: calc(97px - 5em);
    }
        
    .menu-card-wrapper {
        min-width: 30%;
    }

    .menu-wrapper {
        flex-wrap: wrap
    }

}
