.contact-wrapper {
	/* padding: 0 3px; */
	margin: 0 auto;
	text-align: center;
}

.contact-wrapper .hours img,
.contact-wrapper .time img,
.contact-wrapper .email img {
	margin: auto;
}

.contact-wrapper .card {
	/* padding: 12px 12px 24px;
	border-radius: 24px; */
	box-shadow: 0 0 0 0;
}

.email-submit button {
	margin-top: 1em;
	color: #ffffff;
	background-color: #0194f6;
	box-shadow: 0 0 0 0;
}

.email-submit button:hover {
	color: #ffffff;
	background-color: #0194f6;
}

.contact-wrapper h3 {
	margin: 24px 12px 12px;
}

.form-text {
	margin-bottom: 0;
}

.contact-form {
	padding: 0 3px;
}

@media screen and (min-width: 766px) {

	.phone.spacer-md {
		display: none;
	}

	.social-icons-wrapper {
		display: none;
	}

	.contact-wrapper .card {
		padding: 0;
		border-radius: 0;
	}

	.contact-wrapper {
		margin-top: 0px;
		padding: 0;
	}

	.inner-contact {
		max-width: 1138px;
		margin-left: auto;
		margin-right: auto;
	}

	.contact-wrapper .inner-card.hours {
		border-top: 0;
		border-bottom: 0;
	}

	.contact-text {
		margin: 24px 0;
		display: flex;
		justify-content: space-evenly;
	}

	.contact-wrapper h3 {
		margin: 0;
	}

	.contact-text p {
		margin: 0;
	}

	.inner-card {
		text-align: center;
	}

	.inner-card img {
		margin-left: auto;
		margin-right: auto;
	}

	.contact-form {
		text-align: center;
	}

}
 