.cart-wrapper {
    text-align: left;
    margin-top: 87px;
}

.cart-wrapper h1 {
    text-align: center;
}

.cart-wrapper h2 {
    margin: 36px 0 12px 24px;
}

.cart-wrapper h1:first-child {
    margin-top: 24px;
}

.cart-meals {
    color: #00D100;
}

.cart-entrees {
    color: #0093FA
}

.cart-sides {
    color: #FF7065
}

.cart-drinks {
    color: #96CDFD
}

.cart-wrapper p {
    margin-left: 24px;
    margin-top: 0;
}

.cart-remove-item {
    width: 20px;
    height: 20px;
}

.cart-item-name {
    display: inline-block;
    margin-left: 24px;
}

.cart-wrapper .cart-delete-button {
    background-color: #ff0000;
    box-shadow: none;
}

.cart-wrapper .cart-delete-button:hover {
    background-color: #ff0000;
}

.cart-wrapper .cart-add-button {
    background-color: #00D100;
    box-shadow: none;
}

.cart-wrapper .cart-add-button:hover {
    background-color: #00D100;
}

.cart-wrapper .cart-subtract-button {
    background-color: #0194F6;
    box-shadow: none;
}

.cart-wrapper .cart-subtract-button:hover {
    background-color: #0194F6;
}

.cart-button-wrapper {
    display: flex;
    justify-content: space-evenly;
    margin-bottom: 24px;
}

@media screen and (min-width: 1366px) {

    .cart-wrapper {
        margin-top: 90px;
        /* margin-bottom: 142px; */
    }

    .cart-item-wrapper {
        display: grid;
        grid-template: 1fr / 1fr;
    }

    .cart-item-name {
        grid-row: 1;
        grid-column: 1;
    }

    .cart-card-wrapper {
        max-width: 800px;
        margin-left: auto;
        margin-right: auto;
    }
    
    .cart-button-wrapper {
        grid-row: 1;
        grid-column: 2;
        margin-right: 24px;
        width: 200px;
    }
}